@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
@import url(https://fonts.googleapis.com/css?family=DM+Sans:400,500|Jost:400,500,600&display=swap);
/* font-family: 'Rubik', sans-serif; */

:root {
    --fontSize10: 10px;
    --fontSize12: 12px;
    --fontSize15: 15px;
    --fontSize20: 20px;
    --fontSize25: 25px;
    --fontSize30: 30px;
    --fontSize35: 35px;
    --fontSize40: 40px;
}

* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: "poppins", sans-serif;
    scroll-behavior: smooth;
}

.icon>span>a:link,
.icon>span>a:visited,
.icon>span>a:hover,
.icon>span>a:active {
    color: white;
}

/* Applied on full app */
div {
    color: white;
    padding: 12px;
}

/* Applied to only summary Section div */
#summarySection div {
    /* color: #371db8; */
    color: #37439c;
    padding: 5px;
}

body {
    background: black;
    font-family: "Rubik", sans-serif;
    font-weight: 700;
    width: 100%;
    position: relative;
}

/* Navbar starts */
.navbar {
    color: white;
    padding: 15px 25px;
    top: 0;
    left: 0;
    background-color: black;
    width: 100%;
    height: 70px;
    position: fixed;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: center;
    align-content: center;
    z-index: 14;
    box-shadow: 0 1px 5px 0px #ffffff36;
}

.navbar>h1 {
    cursor: pointer;
}

.menus>h3>a {
    text-decoration: none;
    color: white;
}

.navbar>.hamburger {
    display: none;
    position: relative;
    font-size: 30px;
    cursor: pointer;
    padding: 3px 12px;
    min-height: 40px;
    min-width: 40px;
    cursor: pointer;
}

.navbar>div {
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: nowrap;
    flex-direction: row;
}

.navbar>div>h3 {
    padding: 3px 12px;
    cursor: pointer;
}

.navbar>div>h3>a:hover {
    color: #f38332;
    background-color: #141414f0;
}

/* Navbar ends */

.main {
    width: 100%;
}

.firstView {
    width: 100%;
    min-height: 96vh;
    height: 96vh;
    padding: 10px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    /* for scroll Down action */
    position: relative;
}

.firstView>div {
    /* min-width: 440px; */
    width: 49%;
    padding: 12px;
    /* border: 2px solid white; */
}

.whoIAm {
    display: flex;
    /* min-width: 480px !important; */
    flex-direction: column;
    font-size: 30px;
    text-transform: uppercase;
    justify-content: center;
    align-content: flex-start;
    align-items: flex-start;
    flex-wrap: nowrap;
    position: relative;
}

#myAvatar {
    padding: 0;
    position: absolute;
    top: 26%;
    /* right: 32%; */
    right: 22%;
    -webkit-transform: rotate(-13deg) translate(50%, 10px);
            transform: rotate(-13deg) translate(50%, 10px);
    width: 180px;
    height: 210px;
    /* border: 1px solid #ff8918; */
    overflow: hidden;
    border-radius: 26% 88px 0px;
    cursor: grabbing;
    /* display: inline-block; */
}

#myAvatar>img {
    width: 100%;
    height: auto;
}

.name {
    color: #e7e4df;
    background-color: black;
    /* background-color: #343f4f; */
}

.wrapper {
    display: inline-flex;
}

.wrapper .static-txt {
    color: #fff;
    padding: 0;
    font-size: 28px;
    display: block;
    color: transparent;
    text-decoration: underline;
    text-underline-position: from-font;
    -webkit-text-decoration-color: rgb(255 0 0 / 70%);
            text-decoration-color: rgb(255 0 0 / 70%);
    padding: 0;
    -webkit-text-stroke: 0.2px white;
}

.wrapper .dynamic-texts {
    height: 30px;
    line-height: 31px;
    overflow: hidden;
    justify-content: center;
    margin-left: 7px;
    margin-top: 1px;
    z-index: -1;
}

.dynamic-texts li {
    color: #fa5151;
    list-style: none;
    font-size: 25px;
    font-weight: 500px;
    position: relative;
    top: 0;
    -webkit-animation: slide 9.6s steps(4) infinite;
            animation: slide 9.6s steps(4) infinite;
}

.whoIamPic {
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
}

.whoIamPic>div {
    display: flex;
    width: 100%;
    height: 100%;
    align-content: center;
    /* box-shadow: inset 0 0 7px 5px #343f4f; */
}

.whoIamPic>div>img {
    object-fit: contain;
    height: 100%;
    width: 100%;
    display: block;
    margin: 0 auto;
    -webkit-filter: grayscale(25%) brightness(80%);
            filter: grayscale(25%) brightness(80%);
    box-shadow: 0 0 0 0 black;
    z-index: -1;
}

@-webkit-keyframes slide {
    100% {
        top: -120px;
    }
}

@keyframes slide {
    100% {
        top: -120px;
    }
}

.dynamic-texts li>span {
    position: relative;
    height: 30px;
}

.dynamic-texts li>span::after {
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    border-left: 2px solid #fc6d6d;
    left: 0;
    /* background-color: #343f4f; */
    background-color: black;
    /* animation: typing 2s steps(14) infinite; */
    -webkit-animation: typing 1.2s steps(14) infinite alternate;
            animation: typing 1.2s steps(14) infinite alternate;
}

@-webkit-keyframes typing {
    100% {
        left: 100%;
        margin: 0 -35px 0 35px;
    }
}

@keyframes typing {
    100% {
        left: 100%;
        margin: 0 -35px 0 35px;
    }
}

/* Social Icons start*/
.socialIcons {
    /* margin-top: 20px; */
    margin-top: 9%;
    margin-left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    display: inline-flex;
    /* background-color: #e94b10; */
    background-color: #ff8800;
    border-radius: 50px;
    padding: 3px;
}

.socialIcons .icon {
    margin: 0;
    padding: 3.5px 12px;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: relative;
    z-index: 2;
    cursor: pointer;
}

.socialIcons .icon span {
    position: relative;
    /* height: 20px; */
    /* width: 20px; */
    display: block;
    /* background: black; */
    text-align: center;
    border-radius: 50%;
    z-index: 2;
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
}

.socialIcons .icon span i {
    font-size: 22px;
    line-height: 20px;
    padding: 0;
}

.socialIcons .icon .tooltip {
    position: absolute;
    top: 0px;
    /* background: #0f1124; */
    background: white;
    color: black;
    font-weight: bold;
    font-size: 10px;
    padding: 9px 18px 12px;
    border-radius: 25px;
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.socialIcons .icon:hover .tooltip {
    top: -35px;
    opacity: 1;
    pointer-events: all;
}

.socialIcons .icon .tooltip::before {
    position: absolute;
    z-index: 1;
    content: "";
    height: 15px;
    width: 15px;
    /* background: #0f1124; */
    background: white;
    bottom: -8px;
    left: 50%;
    transition: all 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    -webkit-transform: translateX(-50%) rotate(45deg);
            transform: translateX(-50%) rotate(45deg);
}

.socialIcons .icon:hover span,
.socialIcons .icon:hover .tooltip {
    text-shadow: 0px -1px 0px rgba(255, 255, 255, 0.4);
}

/* Social Icons ends*/

/* Scroller starts*/
#scroller {
    /* border: 1px solid white; */
    position: absolute;
    bottom: 4px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: flex-start;
    align-items: center;
    width: auto;
    font-size: 0.75rem;
    cursor: context-menu;
}

#scroller>div {
    text-align: center;
    font-size: x-large;
    border: 1px solid white;
    border-radius: 17px;
    box-shadow: inset 1px 1px 2px 0 #e1dfdf, inset -1px -1px 2px 0 #c1b9b9;
    width: 15px;
    height: 35px;
    padding: 0;
    text-align: center;
    -webkit-animation: scrollDot 2.5s linear infinite;
            animation: scrollDot 2.5s linear infinite;
    margin-top: 6px;
    cursor: context-menu;
}

#scroller>div>span {
    -webkit-animation: scrollDot2 2.5s linear infinite;
            animation: scrollDot2 2.5s linear infinite;
    box-shadow: 0 0 10px 3px black;
    border-radius: 20px;
    cursor: context-menu;
}

@-webkit-keyframes scrollDot {
    0% {
        padding-top: 0px;
    }

    45% {
        padding-top: 12px;
    }

    65%,
    100% {
        padding-top: 15px;
    }
}

@keyframes scrollDot {
    0% {
        padding-top: 0px;
    }

    45% {
        padding-top: 12px;
    }

    65%,
    100% {
        padding-top: 15px;
    }
}

@-webkit-keyframes scrollDot2 {
    0% {
        opacity: 1;
    }

    45% {
        opacity: 0;
    }

    75%,
    99% {
        opacity: 0;
    }
}

@keyframes scrollDot2 {
    0% {
        opacity: 1;
    }

    45% {
        opacity: 0;
    }

    75%,
    99% {
        opacity: 0;
    }
}

/* Scroller starts ends*/

