	border-radius: 50%;
	object-position: center;
	object-fit: cover;
	position: absolute;
	bottom: 0;
	left: 50%;
	-webkit-transform: translateX(-50%) translateY(-64px);
	        transform: translateX(-50%) translateY(-64px);
